import uCharts from '@/components/u-charts/u-charts.js';
export default {
	data() {
		return {
			title: 'Hello'
		}
	},
	onLoad() {
		let that = this
		uni.getLocation({
			type: 'wgs84',
			success(res) {
				console.log(res)
				that.showChart()
			}
		})
		

	},
	methods: {
		showChart() {
			let _self = this
			let chartData = {
				categories: ['星期一', '星期二', '星期三', '星期', '星期二', '星期二'],
				series: [{
					name: 'min',
					data: [22, 23, 25, 26, 28, 26],
					color: '#FFFFFF'
				}, {
					name: 'max',
					data: [30, 31, 32, 29, 29, 30],
					color: '#FFFFFF'
				}]
			}
			let canvasTemperature = new uCharts({
				$this: _self,
				canvasId: 'canvasTemperature',
				type: 'line',
				legend: {
					show: false
				},
				dataLabel: false,
				dataPointShape: false,
				padding:[0,0,0,0],
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				xAxis: {
					disabled: true,
					disableGrid: true
				},
				yAxis: {
					disabled: true,
					disableGrid: true
				},
				width: uni.upx2px(710),
				height: uni.upx2px(200),
				extra:{
					line:{
						type:'curve'
					}
				}
			});

		}
	}
}
